.registration {
  max-width: 320px;
  padding-left: 25px;
  padding-right: 25px;
  margin-right: auto;
  margin-left: auto;
  text-align: center;
  background-color: rgb(245, 239, 206);
  box-shadow: 0 4px 4px rgb(0 0 0 / 25%);
  border-radius: 6px;
  overflow: hidden;
}

.register__form {
  width: 320;
}

.register__label {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin-bottom: 15px;
}

.input {
  width: 100%;
  padding: 5px;
  font-weight: 500;

  border: 2px solid gray;
  border-radius: 4px;
}

.input:hover,
.input:focus {
  border: 2px solid #2196f3;
  cursor: pointer;
}

.button {
  background-color: darkgray;
  color: white;
  border-radius: 4px;
  border-color: darkgrey;
  cursor: pointer;
  margin-bottom: 10px;
  box-shadow: azure;
}
.button:hover,
.button:focus {
  background-color: dimgrey;
}
.text {
  color: rgb(96, 95, 95);
}
